import * as React from 'react';
import styled from 'styled-components';
import * as constants from '../../contsants';

export const ContactDetails: React.FunctionComponent<{
  isOpened: boolean;
}> = props => {
  const emailMe = 'mailto:"smithkye@live.co.uk?subject=Hey Kye!';
  const linkMe = 'https://uk.linkedin.com/in/kye-smith-121429165';

  return (
    props.isOpened && (
      <>
        <StyledLink>
          <a href={emailMe}>Email me</a>
        </StyledLink>
        <br/>
        <br/>
        <StyledLink>
          <a href={linkMe} target="_blank">
            Find me on LinkedIn
          </a>
        </StyledLink>
      </>
    )
  );
};

export const StyledLink = styled.span`
  display: inline-block;
  width: 20vw;
  font-family: 'Baloo Chettan';
  font-size: 1.5vw;
  color: ${constants.colors.theme.teal};
  text-decoration: underline;

  > a {
    color: ${constants.colors.theme.teal};

    &:hover {
      cursor: pointer;
      color: ${constants.colors.theme.pinkSand};
    }
  }

  @media screen and (max-width: 600px) {
    width: 30vw;
    font-size: 3vw;
  }
`;
